/**
 * Reset some basic elements
 */
 @import url('https://fonts.googleapis.com/css2?family=Cousine:ital,wght@0,400;0,700;1,400;1,700&family=Fira+Code:wght@300..700&display=swap'); 

 $bg:#353535;
 $fg:#DDDDDD;
 $lc:#94C4C7;
 $lv:#284B63;
 $lh:#D9D9D9;
 $cfg:#B7D2E1;
 $cbg:#070D0D;
 $tbg:#404040;
 $tbh:#090909;
 $tce:#505050;
 $ffa:"Cousine", monospace;

 html {
   font-size: 14px;
 }

 body, h1, h2, h3, h4, h5, h6,
 p, blockquote, pre, hr,
 dl, dd, ol, ul, figure {
   margin: 0;
   padding: 0;
 }
 
 /**
  * Basic styling
  */
 body {
   font-family: $ffa;
   font-optical-sizing: auto;
   font-style: normal;
   font-weight: 400;
   line-height: 1.60em;
   color: $fg;
   background-color: $bg;
   -webkit-text-size-adjust: 100%;
   -webkit-font-feature-settings: "kern" 1;
   -moz-font-feature-settings: "kern" 1;
   -o-font-feature-settings: "kern" 1;
   font-feature-settings: "kern" 1;
   font-kerning: normal;
   display: flex;
   min-height: 100vh;
   flex-direction: column;
 }
 
 /**
  * Set `margin-bottom` to maintain vertical rhythm
  */
 h1, h2, h3, h4, h5, h6,
 p, blockquote, pre,
 ul, ol, dl, figure,
 .highlight {
   margin-bottom: 15px;
 }
 
 /**
  * `main` element
  */
 main {
   display: block; /* Default value of `display` of `main` element is 'inline' in IE 11. */
 }
 
 /**
  * Images
  */
 img {
   max-width: 100%;
   vertical-align: middle;
 }
 
 /**
  * Figures
  */
 figure > img {
   display: block;
 }
 
 figcaption {
 }
 
 /**
  * Lists
  */
 ul, ol {
   margin-left: 0;
 }
 
 li > ul,
 li > ol {
   margin-bottom: 0;
 }

 ul li  {
  margin-left: 15px;
  list-style-type: disc;
  border-bottom: 1px solid #444;
 }

 ol li {
  margin-left: 25px;
 } 

 /**
  * Headings
  */
 h1, h2, h3, h4, h5, h6 {
   font-weight: 400;
 }
 
 /**
  * Links
  */
 a {
   color: $lc;
   text-decoration: none;
 }
 a:visited {
   color: $lc;
  }
 a:hover {
   color: $lh;   
   text-decoration: underline;
 }
 .social-media-list a:hover {
   text-decoration: none;
 }
 .social-media-list a:hover .username {
   text-decoration: underline;
 }
 
 /**
  * Blockquotes
  */
 blockquote {
   color: #828282;
   border-left: 4px solid #e8e8e8;
   padding-left: 14px;
   letter-spacing: -1px;
 }
 blockquote > :last-child {
   margin-bottom: 0;
 }
 
 /**
  * Code formatting
  */
 pre,
 code {
   font-size: 0.85rem;
   border: 1px social-media-listolid #222;
   border-radius: 3px;
   color: $cfg;
   background-color: $cbg;
 }
 
 code {
   padding: 1px 5px;
 }
 
 pre {
   padding: 8px 12px;
   overflow-x: auto;
 }

 pre > code {
   border: 0;
   padding-right: 0;
   padding-left: 0;
 }

 hr {
  color: #444;
 }
 .video-container {
   position: relative;
   padding-bottom: 56.25%;
 }
 
 .video-container iframe {
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
 }
 
 .pager ul.pagination li {
     float: left;
     list-style-type: none;
     padding: 0 0 0 0;
     background-color: #f0f0f0;
 }

.pagination {
}

.breadcrumbs {
  border-top: 1px solid #444;
  border-bottom: 1px solid #444;
  padding:10px 0;
  margin-bottom:20px;
}

.breadcrumbs-url {
  color:#828282;
}

 #search-container input {
   width: 40%;
   padding:10px;
   margin:0;
   color: $lc;
   background-color: $tbg;
   border: 1px solid #444;
 }
 
 ul#results-container {
   margin-top:20px;
 }
 
 ul#results-container li {
   margin-top:7px;
 }
 
 .date {
   color: #888;
 }
 
 .highlight code {
   font-weight: 400;
   line-height: 0.5em;
 }
 
 strong {
   color: #73BBA3;
   font-style: bold;
 }

 .multicolumn {
   display: flex;
   flex-wrap: wrap;
   gap: 20px;
 }
 .column {
   flex: 1 1 45%;
 }

/* Container for the two-column layout */
.blog-posts-container {
  display: grid;
  grid-template-columns: 1fr 1fr; 
  gap: 10px 30px; 
}

/* Style for each post item */
.post-item {
  margin-bottom: 0; 
  line-height: 1.2;
  border-bottom: 1px solid #444; 
}

/* Optional: Add responsiveness for mobile devices */
@media (max-width: 960px) {
  .blog-posts-container {
    grid-template-columns: 1fr; 
    gap: 10px 0;
  }
} 

@media (max-width: 1080px) {
  .column {
    flex: 1 1 100%;
  }
}

 /**
  * Wrapper
  */
 .wrapper {
   max-width: -webkit-calc(1280px - (30px * 2));
   max-width: calc(1280px - (30px * 2));
   margin-right: auto;
   margin-left: auto;
   padding-right: 30px;
   padding-left: 30px;
 }
 @media screen and (max-width: 1200px) {
   .wrapper {
     max-width: -webkit-calc(1280px - (30px));
     max-width: calc(1280px - (30px));
     padding-right: 15px;
     padding-left: 15px;
   }
 }
 
 /**
  * Clearfix
  */
 .footer-col-wrapper:after, .wrapper:after {
   content: "";
   display: table;
   clear: both;
 }
 
 /**
  * Icons
  */
 .svg-icon {
   width: 16px;
   height: 16px;
   display: inline-block;
   fill: #828282;
   padding-right: 5px;
   vertical-align: text-top;
 }
 
 .social-media-list li + li {
   padding-top: 5px;
 }
 
 /**
  * Tables
  */

 table {
   margin-bottom: 30px;
   color: $fg;
   background-color: $tbg;
   width: 100%;
   text-align: left;
   border-collapse: collapse;
   border: 1px solid #222;
 }

 table tr:nth-child(even) {
   background-color: $tce;
 }

 table th, table td {
   padding: 10px 15px;
 }

 table th {
   background-color: $tbh;
   border: 1px solid #100b0d;
   border-bottom-color: #100b0d;
 }

 table td {
   border: 1px solid #302f31;
 }
 
 /**
  * Site header
  */
 .site-header {
   border-top: 5px solid #424242;
   border-bottom: 1px solid #424242;
   min-height: 55.95px;
   position: relative;
 }
 
 .site-title {
   font-size: 2.0rem;
   font-weight: 300;
   line-height: 64px;
   letter-spacing: -1px;
   margin-bottom: 0;
   float: left;
 }
 .site-title, .site-title:visited {
   color: #ffffff;
 }

.site-title:hover {
  color: $lh;
}

 .site-nav {
   float: right;
   line-height: 54px;
 }
 .site-nav .nav-trigger {
   display: none;
 }
 .site-nav .menu-icon {
   display: none;
 }
 .site-nav .page-link {
   color: $lc;
   line-height: 1.5;
 }
 .site-nav:hover .page-link:hover {
  color: $lh;
  line-height: 1.5;
} 
 .site-nav .page-link:not(:last-child) {
   margin-right: 20px;
 }
 @media screen and (max-width: 600px) {
   .site-nav {
     position: absolute;
     top: 9px;
     right: 15px;
     background-color: $bg;
     border: 1px solid $cfg;
     border-radius: 5px;
     text-align: right;
   }
   .site-nav label[for=nav-trigger] {
     display: block;
     float: right;
     width: 36px;
     height: 36px;
     z-index: 2;
     cursor: pointer;
   }
   .site-nav .menu-icon {
     display: block;
     float: right;
     width: 36px;
     height: 26px;
     line-height: 0;
     padding-top: 10px;
     text-align: center;
   }
   .site-nav .menu-icon > svg {
     fill: $lc;
   }
   .site-nav input ~ .trigger {
     clear: both;
     display: none;
   }
   .site-nav input:checked ~ .trigger {
     display: block;
     padding-bottom: 5px;
   }
   .site-nav .page-link {
     display: block;
     padding: 5px 10px;
     margin-left: 20px;
   }
   .site-nav .page-link:not(:last-child) {
     margin-right: 0;
   }
 }
 
 /**
  * Site footer
  */
 .site-footer {
   border-top: 1px solid #424242;
   padding: 30px 0;
 }
 
 .footer-heading {
   margin-bottom: 15px;
 }
 
 .contact-list,
 .social-media-list {
   list-style: none;
   margin-left: 0;
 }
 
 .footer-col-wrapper {
   color: #ECDFCC;
   margin-left: -15px;
 }
 
 .footer-col-right {
   float: right;
   margin-bottom: 15px;
   padding-left: 15px;
 }
 
 .footer-col {
   float: left;
   margin-bottom: 15px;
   padding-left: 15px;
 }
 
 .footer-col-1 {
   width: -webkit-calc(35% - (30px / 2));
   width: calc(35% - (30px / 2));
 }
 
 .footer-col-2 {
   width: -webkit-calc(20% - (30px / 2));
   width: calc(20% - (30px / 2));
 }
 
 .footer-col-3 {
   width: -webkit-calc(45% - (30px / 2));
   width: calc(45% - (30px / 2));
 }
 
 @media screen and (max-width: 800px) {
   .footer-col-1,
   .footer-col-2 {
     width: -webkit-calc(50% - (30px / 2));
     width: calc(50% - (30px / 2));
   }
   .footer-col-3 {
     width: -webkit-calc(100% - (30px / 2));
     width: calc(100% - (30px / 2));
   }
 }
 @media screen and (max-width: 600px) {

   .numbers {
     display: none;
   }
   .footer-col-right {
     float: none;
     margin-bottom: 15px;
     padding-left: 15px;
   }
   .footer-col {
     float: none;
     width: -webkit-calc(100% - (30px / 2));
     width: calc(100% - (30px / 2));
   }
 }
 /**
  * Page content
  */
 .page-content {
   padding: 30px 0;
   flex: 1;
 }

 .page-content h1 {
  font-size: 1.5rem;
}

 .page-content h4 {
   line-height: 1.5em;
   padding: 0;
   margin: 1px 0;
 }

 .page-content h4 a {
   font-size: 2rem;
 }
 
 .page-heading {
   font-size: 2rem;
 }
 
 .post-list-heading {
   font-size: 2rem;
 }
 
 .post-list {
   margin-left: 0;
   list-style: none;
 }
 .post-list > li {
   margin-bottom: 30px;
 }
 
 .post-meta {
   color: #828282;
 }
 
 .post-link {
   display: block;
   font-size: 1.5rem;
 }
 
 /**
  * Posts
  */
 .post-header {
   margin-bottom: 30px;
 }
 
 .post-title {
   font-size: 1.5rem;
   letter-spacing: -1px;
   line-height: 1;
 }

 @media screen and (max-width: 800px) {
   .post-title {
     font-size: 1.5rem;
   }
 }

 .post-content ul li {
   border:none;
 }
 
 .post-content {
   margin-bottom: 30px;
 }
 .post-content h2 {
   font-size: 2rem;
 }
 @media screen and (max-width: 800px) {
   .post-content h2 {
     font-size: 1.5rem;
   }
 }
 .post-content h3 {
   font-size: 1.5rem;
 }
 @media screen and (max-width: 800px) {
   .post-content h3 {
     font-size: 1.5rem;
   }
 }
 .post-content h4 {
 }
 @media screen and (max-width: 800px) {
   .post-content h4 {
     font-size: 1.5rem;
   }
 }
 
